import { useContext } from 'react';
import { ToolbarContext, DoubleLineRowContext } from '../../../layout/contexts';

interface IToolbarItemConfig {
  itemSize: 'small' | 'normal' | 'large';
}

export const useToolbarItemConfig = (): IToolbarItemConfig => {
  const { mode } = useContext(ToolbarContext);
  const inDbRow = useContext(DoubleLineRowContext);

  let itemSize: 'normal' | 'large' | 'small';

  if (mode === 'singleLine') {
    itemSize = 'normal';
  } else if (inDbRow) {
    itemSize = 'small';
  } else {
    itemSize = 'large';
  }

  return {
    itemSize,
  };
};
